.status {
    margin-bottom: 10px;
    }

.square {
    background: rgba(56, 204, 160, 0.712);
    border: 2px solid rgb(135, 240, 222);
    float: left;
    font-size: 3em;
    font-weight: bold;
    color: #fff;
    line-height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
    height: 100px;
    }

.square:focus {
    outline: none;
    }

.kbd-navigation .square:focus {
    background: #ddd;
    }